	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"T Minus 5 Weeks","date":"28 May 2007","slug":"/2007-05-28-t-minus-5-weeks/","tags":"Y2007"},"html":"<p>Five weeks from yesterday we go officially into business. We're making continual progress... financing, staffing, and remodeling plans are on track, but not yet set. Today we looked at the newly remodeled restaurant in Bispingen with a \"Gym and Fun\", 30 sq. m indoor activity area with basketball hoop and padded obstacle course. Christopher was very impressed, and we'd love to have one too. We'll see what the architect (and the bank) have to say.</p>\n<p>We've been trying to think of a title for me, someone who does a little of everything as needed, but is not the boss. Today we came up with \"General Manager\". Now I can finally order business cards. :-)</p>\n<p>Frauke works nights this week with equipment maintenance and monthly reports on her training schedule. Hopefully Christopher won't wake us up at 6:45 like he did this morning.</p>"},"next":{"fields":{"title":"Pfingstsingen","date":"27 May 2007","slug":"/2007-05-27-pfingstsingen/"}},"previous":{"fields":{"title":"Shock and Awe","date":"31 May 2007","slug":"/2007-05-31-shock-and-awe/"}}},{"node":{"fields":{"title":"Pfingstsingen","date":"27 May 2007","slug":"/2007-05-27-pfingstsingen/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/515747259/\" title=\"Photo Sharing\"><img src=\"https://farm1.static.flickr.com/239/515747259_d15ea7584f.jpg\" width=\"500\" height=\"375\" alt=\"IMG_2056\" /></a></p>\n<p><a href=\"http://www.flickr.com/photos/papascott/515748745/\" title=\"Photo Sharing\"><img src=\"https://farm1.static.flickr.com/220/515748745_fdaecf8f8c_m.jpg\" width=\"240\" height=\"180\" alt=\"CIMG0409\" /></a></p>\n<p>They were lucky, the rain let up this morning. <a href=\"http://www.flickr.com/photos/papascott/sets/72157600271055815/\">More pictures of the wagon</a>.</p>"},"next":{"fields":{"title":"","date":"26 May 2007","slug":"/2007-05-26-it-was-thirty-years-ago/"}},"previous":{"fields":{"title":"T Minus 5 Weeks","date":"28 May 2007","slug":"/2007-05-28-t-minus-5-weeks/"}}},{"node":{"fields":{"title":"","date":"26 May 2007","slug":"/2007-05-26-it-was-thirty-years-ago/","tags":"Y2007"},"html":"<p>Hal remembers that 30 years ago this weekend:</p>\n<blockquote><p>\n  The movie Star Wars (AKA: Star Wars: A New Hope) opened in theaters across the country and set the record for the largest grossing film to date. In my mind, which seldom accounts for much, there are only <em>real</em> three Star Wars movies called Star Wars, The Empire Strikes Back and Return of the Jedi. None of them have the prefix Star Wars:.\n</p></blockquote>\n<p>He also has a link to the original review in the New York Times: <a href=\"http://movies2.nytimes.com/mem/movies/review.html?_r=2&amp;title1=&amp;title2=Star%20Wars%20%28Movie%29&amp;reviewer=VINCENT%20CANBY&amp;pdate=19770526&amp;oref=slogin&amp;oref=login\">Star Wars A Trip to a Far Galaxy Thats Fun and Funny</a>.</p>\n<p>Christopher is, shall we say, intensely familiar with both trilogies and contributes most of his allowance money to Lego for their line of Star Wars sets.</p>\n<p>As for me, Star Wars was my <em>second</em> favorite movie while in high school. The movie that most entertained and inspired me as teenager came out a year later... <a href=\"http://imdb.com/title/tt0077975/\">Animal House</a>.</p>"},"next":{"fields":{"title":"Bolle took a trip for Pentecost","date":"26 May 2007","slug":"/2007-05-26-bolle-took-a-trip-for-pentecost/"}},"previous":{"fields":{"title":"Pfingstsingen","date":"27 May 2007","slug":"/2007-05-27-pfingstsingen/"}}}],"pathPrefix":"","first":false,"last":false,"index":123,"pageCount":1101}}

